.seccion-consulta {
    width: 100%;
    height: auto;
    margin: auto auto;
}

.contenedor-titulo-tabla-calificaciones {
  height: 25px;
  background: rgb(255,170,0);
  background: linear-gradient(43deg, rgba(255,170,0,1) 0%, rgba(255,170,0,0.7766456924566701) 71%);
  border: none;
}

.titulo-tabla-calificaciones {
  margin: auto;
  padding-left: 20px;
}

.seccion-tabla-calificaciones {
    height: auto;
    margin: auto;
    padding: 0px 20px;
  }
  
  .contenedor-tabla-calificaciones {
    height: auto;
  }
  
  .tabla-calificaciones {
    border-collapse: collapse;
    width: 100%;
  }
  
  .tabla-calificaciones td {
    padding: 8px 5px;
    font-size: 12px;
  }
  
  .tabla-calificaciones tr {
    background-color: rgb(255, 255, 255);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
  }
  
  .tabla-calificaciones tr:hover {
    background-color: rgba(0, 0, 0, 0.03);
  }
  
  .tabla-calificaciones th {
    width: auto;
    font-size: 14px;
    padding: 13px 5px;
    text-align: left;
    background-color: #1a73e8be;
    color: white;
  }