.contenedor-botones {
    width: 100%;
    height: auto;
    padding-top: 5px;
    padding-bottom: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-bottom: 1px solid rgb(230, 236, 240);
}

.seccion-botones {
    height: auto;
    margin: auto;
}

.seccion-botones-calificaciones {
    display: flex;
    align-items: center;
    text-align: center;
}

.boton-calificaciones {
    margin-right: 1rem;
    max-height: 35px;
    border: 2px solid rgba(29, 161, 242, 0.76);
    outline: none;
    color: white;
    font-size: 15px;
    font-weight: bold;
    background-color: rgba(29, 161, 242, 0.76);
    padding: 5px 20px;
    cursor: pointer;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    border-bottom-left-radius: 9999px;
    border-bottom-right-radius: 9999px;
    border-top-left-radius: 9999px;
    border-top-right-radius: 9999px;
}
  
.boton-calificaciones:hover, .boton-calificaciones:focus, .boton-calificaciones:active {
    transition-duration: .1s, .5s;
    transition-timing-function: linear, ease-in-out;
    background-color: rgb(29, 160, 242);
    border: 2px solid rgb(29, 160, 242);
    color: white;
}

.boton-impresora {
    padding: 8px;
    display: flex;
    justify-content: left;
    align-items: center;
    text-align: center;
    cursor: pointer;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    border-bottom-left-radius: 9999px;
    border-bottom-right-radius: 9999px;
    border-top-left-radius: 9999px;
    border-top-right-radius: 9999px;
}

.boton-impresora:hover {
    transition-duration: .1s, .1s;
    transition-timing-function: linear, ease-in-out;
    background-color: rgba(29, 161, 242, 0.1);
}